<template>
  <form id="searchForm" onsubmit="return false">
    <div class="input-group">
      <input type="hidden" name="page" value="1">
      <div class="input-group-prepend">
        <select class="form-select" v-model="myoption">
          <option value="name" selected>제목으로 그룹 찾기</option>
          <option value="content">내용으로 그룹 찾기</option>
        </select>
      </div>
      <input v-model="string2search" class="form-control" v-on:keyup.enter="launchSearch()">
      <div class="input-group-append" id="button-addon4">
        <button class="btn btn-outline-secondary searchBtn" type="button" @click="launchSearch()">검색</button>
        <button class="btn btn-outline-secondary searchBtn" type="button" @click="handleGetList()">취소</button>
      </div>
    </div>
  </form><br>
  <div class="row">
    <div class="card border-success mb-3" style="max-width: 18rem;">
      <div class="card-body ">
        <p class="card-text "></p>
        <p class="fs-4" style="margin-top:2em;margin-bottom: em;">
          <RouterLink to="/circlemaker" style="text-decoration:none; color:#30353d;">
            <img src="../assets/icones/plus-circle-fill.svg" style="width:2em;" alt="join a new circle"
              class="aligncenter text-success filter-green">&nbsp;&nbsp;나의 그룹 만들기
          </RouterLink>
        </p>
      </div>
    </div>
    <div class="card border-success mb-3" style="max-width: 18rem;" v-for="(list, i) in state.lists" :key="(list, i)">
      <h5 class="card-header ">
        <RouterLink :to="{name: 'circleread', params:{id:list.circleid}}"
          style="text-decoration:none;color:#30353d;">{{list.name}}</RouterLink>
        <!-- <span class="fs-5">&nbsp&nbsp{{list.memberCount}}/{{list.maxNum}}</span>
        <img alt="user icon" src="../assets/icones/people.jpg"> -->
      </h5>
      <div class="card-body ">
        <p class="card-text ellipsis">{{list.content}}</p>
        <h7 class="card-title">최근 활동 :&nbsp{{formatDate(list.modDate)}}</h7>
        <p class="card__apply" style="font-weight: 1000;">
          <a class="card__link " href="#" v-if="list.pass != ''" data-bs-toggle="modal" data-bs-target="#exampleModal"
            @click="sendId(list.circleid, list.name, list.maxNum, list.memberCount)">지금 가입 </a>
          <a class="card__link" href="#" v-else
            @click="sendId(list.circleid, list.name, list.maxNum, list.memberCount),join()">지금 가입</a>
        </p>
      </div>
    </div>
  </div>
  <!-- Modal -->
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel" ref="mTitle">비밀번호 확인 <div>{{modal.name}} 그룹에 가입</div>
          </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="input-group input-group-sm mb-3">
            <span class="input-group-text" id="inputCircle-sizing-sm">비밀번호</span>
            <input type="text" v-model="state.pass" class="form-control" aria-label="Sizing example input"
              aria-describedby="inputCircle-sizing-sm" autofocus>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">취소</button>
          <button type="button" class="btn btn-primary" data-bs-dismiss="modal" @click="passCheck(modal.id)">확인</button>
        </div>
      </div>
    </div>
  </div> <!-- Modal -->
</template>
<script>
import { onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import dayjs from 'dayjs'
import { useRouter } from 'vue-router'
export default {
  setup() {
    onMounted(() => { handleGetList() })
    let chbox = ref(null)
    const myoption = ref('name')
    const string2search = ref('')
    const router = useRouter();
    const state = reactive({
      lists: '',
      circleid: '',
      name: '',
      pass: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    })
    const modal = reactive({
      id: '',
      name: '',
      maxNum: '',
      memberCount: ''
    })
    const sendId = (id, name, maxNum, memberCount) => {
      modal.id = id
      modal.name = name
      modal.maxNum = maxNum
      modal.memberCount = memberCount
    }
    const handleGetList = async () => {
      const url = 'http://localhost:9090/api/circlelist'
      const headers = {
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
      }
      const body = { email: sessionStorage.getItem('email') }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          state.lists = res.data
        }
      })
    }
    const formatDate = (dateString) => {
      const date = dayjs(dateString);
      return date.format('YY년 MM월 D일');
    }
    const join = async () => {
      if (modal.memberCount >= modal.maxNum) {
        alert("회원이 가득 찼습니다")
      } else {
        const url = 'http://localhost:9090/api/joinCircle'
        const headers = {
          'Content-Type': 'application/json',
          Authorization: state.token,
          token: state.token,
        }
        const body = { circleid: modal.id, email: state.email }
        await axios.post(url, body, { headers }).then(function (res) {
          if (res.status === 200) {
            alert(res.data + "그룹에 가입 되었습니다"); router.push({ name: 'mycircleread', params: { id: modal.id } });
          }
        }).catch(function (error) {
          if (error) {
          }
        })
      }
    }
    const passCheck = async (circleid) => {
      if (modal.memberCount >= modal.maxNum) {
        alert("회원이 가득 찼습니다")
      } else {
        const url = 'http://localhost:9090/api/passCheck'
        const headers = {
          'Content-Type': 'application/json',
          Authorization: state.token,
          token: state.token,
        }
        const body = { circleid: circleid, pass: state.pass }
        await axios.post(url, body, { headers }).then(function (res) {
          if (res.data.dto.pass == state.pass) {
            state.pass = res.data.dto.pass
            state.circleid = res.data.dto.circleid
            join(res.data.dto.circleid);
          } else { alert("not correct"); }
        }).catch(function (error) {
          if (error) {
            alert('비밀번호가 맞지 않습니다.')
          }
        })
      }
    }
    const launchSearch = async () => {
      const url = "http://localhost:9090/api/searchcircle";
      const headers = {
        "Content-Type": "application/json",
        Authorization: sessionStorage.getItem('TOKEN'),
        "token": sessionStorage.getItem('TOKEN')
      };
      const body = { myoption: myoption.value, string2search: string2search.value };
      await axios.post(url, body, { headers }).then(function (res) {
        state.lists = res.data
      }).catch((error) => { console.log("the error :" + error) })
    };
    return { string2search, myoption, launchSearch, state, handleGetList, formatDate, join, passCheck, sendId, modal, chbox }
  },
}
</script>
<style scoped>
input:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
textarea:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
.input-group {
  margin: 10px 0;
}
#searchForm {
  margin-right: 1em;
}
.ellipsis {
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.card {
  border: solid black 1px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px;
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}

.card:hover {
  /* box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4); */
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.4);
  transform: scale(1.01);
}

.card__link,
.card__exit,
.card__icon {
  position: relative;
  text-decoration: none;
  color: rgba(6, 90, 92);
  font-weight: 500;
  /* color: rgba(6, 90, 92, 0.9); */
}

.card__link::after {
  position: absolute;
  top: 25px;
  left: 0;
  content: "";
  width: 0%;
  height: 3px;
  background-color: rgba(6, 90, 92, 0.7);
  transition: all 0.5s;
}

.card__link:hover::after {
  width: 100%;
}

.card__apply {
  grid-row: 5/5;
  align-self: center;
  font-size: large;
}

img {
  width: 1.5em;
}

.filter-green {
  filter: invert(26%) sepia(23%) saturate(2821%) hue-rotate(143deg) brightness(90%) contrast(95%);
}
</style>
